<template>
    <div class="info_container">
        <h3 class="title">{{info.title}}</h3>
        <hr>
        <div class="content" v-html="info.content"></div>
    </div>
</template>
<script>
import { Toast } from 'mint-ui';
import api from '@/api/index.js'
export default {
    data() {
        return {
            info:{}//图文介绍
        }
    },
    created() {
        const goodsId = this.$route.params.id;
        console.log(goodsId);
        this.getGoodsDesc(goodsId);
    },
    methods: {
        getGoodsDesc(goodsId){
            api.getGoodsDesc(goodsId).then(res => {
                console.log('sdfssdg',res);
                if(res.data.status === 0) {
                    this.info = res.data.message[0];
                } else {
                    Toast('请求图文介绍失败');
                }
            })
        }
    },
}
</script>
<style lang="scss">
.info_container{
    padding:0 4px;
    .title{
        font-size: 14px;
        text-align: center;
        margin:15px 0;
        color:#226aff;
    }
    .content{
        img{
            width: 100% !important;
        }
    }
}

</style>



